<template>
    <el-dialog v-dialogDrag :title="dialogTitle" :visible.sync="visible" @close="closeDialog(false)">
        <el-form size="mini" :inline="true">
            <el-form-item label="">
                <el-input v-model="dialogData.parentId" placeholder="父级编码" :maxlength="60" class="inputwinth192">
                    <template slot="prepend">父级编码</template>
                </el-input>
            </el-form-item>
            <el-form-item label="">
                <el-input v-model="dialogData.code" placeholder="编码" :maxlength="60" class="inputwinth192">
                    <template slot="prepend">编码</template>
                </el-input>
            </el-form-item>
            <el-form-item label="">
                <el-input v-model="dialogData.name" placeholder="名称" :maxlength="60" class="inputwinth192">
                    <template slot="prepend">名称</template>
                </el-input>
            </el-form-item>
            <el-form-item label="状态" label-width="80px">
                <el-radio v-model="dialogData.is_Enable" :label="1">启用</el-radio>
                <el-radio v-model="dialogData.is_Enable" :label="0">弃用</el-radio>
            </el-form-item>
            <!-- <el-tabs >
                <el-tab-pane label="下级数据" >
                    <el-row>
                        <el-table
                        :data="tableData"
                        :row-class-name="tableRowClassName"
                        size="mini"
                        v-loading="loadingdata"
                        element-loading-text="正在加载中..."
                        border
                        >
                        <el-table-column type="index" label="序号" width="50"></el-table-column>
                        <el-table-column prop="parentId" label="父级编码" sortable width="150">
                        </el-table-column>
                        <el-table-column prop="code" label="编码"></el-table-column>
                        <el-table-column prop="name" label="名称"></el-table-column>
                        <el-table-column label="状态" width="70">
                            <template slot-scope="prop">
                            <span v-html="prop.row.is_Enable == 1 ? '启用' : '弃用'"></span>
                            </template>
                        </el-table-column>
                        <el-table-column label="操作" width="100">
                            <template slot-scope="prop">
                            <el-button @click="openDialog('编辑', prop.row.id)" type="text">编辑</el-button>
                            </template>
                        </el-table-column>
                        </el-table>
                    </el-row>
                </el-tab-pane>
            </el-tabs> -->
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button size="mini" icon="fa fa-file-o" @click="closeDialog(false)"> 取 消</el-button>
            <el-button size="mini" icon="fa fa-file-text-o" type="primary" @click="saveDialog"> 保 存</el-button>
        </div>
    </el-dialog>
</template>

<script>
export default {
    data() {
        return {
            moduleName: "constant",
            visible: this.dialogShow
        };
    },
    props: {
        dialogTitle: "",
        dialogShow: false,
        dialogData: {
            id: 0,
            code: "",
            name: "",
            is_Enable: 1,
            is_Remove: 0
        }
    },
    methods: {
        closeDialog(isclose) {
            this.$emit("update:dialogShow", isclose);
        },
        saveDialog() {
            var url =
                this.moduleName +
                (this.dialogData.id > 0 ? "/updateOne" : "/insertOne");
            this.$ajax.post(url, this.dialogData).then(res => {
                this.$emit("update:dialogData", res.data);
                this.$emit("update:dialogShow", false);
                this.$emit("RefreshData");
            });
        }
    },
    watch: {
        dialogShow() {
            this.visible = this.dialogShow;
        }
    }
};
</script>



